<template>
 <van-dialog v-model:show="showDialog" title="" class="token-box-dialog" :showConfirmButton="false">
    <div class="token-box">
      <div class="token-top flex-between">
        <div class="title">Select a Token</div>
       <svg  @click="showDialog = false" viewBox="0 0 24 24" class="primary" color="primary" width="20px" xmlns="http://www.w3.org/2000/svg"><path d="M18.3 5.70997C17.91 5.31997 17.28 5.31997 16.89 5.70997L12 10.59L7.10997 5.69997C6.71997 5.30997 6.08997 5.30997 5.69997 5.69997C5.30997 6.08997 5.30997 6.71997 5.69997 7.10997L10.59 12L5.69997 16.89C5.30997 17.28 5.30997 17.91 5.69997 18.3C6.08997 18.69 6.71997 18.69 7.10997 18.3L12 13.41L16.89 18.3C17.28 18.69 17.91 18.69 18.3 18.3C18.69 17.91 18.69 17.28 18.3 16.89L13.41 12L18.3 7.10997C18.68 6.72997 18.68 6.08997 18.3 5.70997Z"></path></svg>
    
      </div>
     <div class="tokenList">
       <van-radio-group v-model="checked" @change="changeSelect">
        <van-radio :name="item.address" v-for="item in tokenList" checked-color="#1FC7D4" icon-size="24px">
          <div class="token-info flex-middle">
            <img :src="require(`@/assets/images/${item.name.toLowerCase()}.png`)" alt="">
            <span>{{item.name}}</span>
          </div>
        </van-radio>
      </van-radio-group>
     </div>
    </div>
    
 </van-dialog>
</template>

<script>
import { reactive, toRefs, computed } from "vue";
import config from "@/utils/config";

export default {
  components: {
  },
  props: {
    show: {
      type: Boolean,
      default: true
    },
    token: {
      type: String,
      default: ''
    }
  },
  setup (props, {emit}) {
    const data = reactive({
      tokenList: [
        {
          name: 'USDT',
          address: config.UsdtAddress
        },
        {
          name: 'USDC',
          address: config.UsdcAddress
        }
      ],
      checked: props.token,
      showDialog: computed({
        get () {
          return props.show
        },
        set (value) {
          emit('updateShow', value)
        }
      }),
      changeSelect: (val) => {
        emit('changeToken', val)
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.primary-btn-box{
 padding: 0 20px;
}
.primary{
  fill: rgb(31, 199, 212);
}
.token-box{
  padding: 20px;
  .title{
     color: var(--primary-color);
     font-size: 16px;
     font-weight: 600;
  }
}
.tokenList{
  padding-top: 20px;
  .token-info{
    padding: 10px 0 10px 12px;
    img{
      width: 30px;
    }
    span{
      font-size: 16px;
      margin-left: 10px;
    }
  }
}
</style>


