<template>
  <div class="create-box">
        <div class="create-btn">
         <div class="btn flex-middle" @click="$router.push({name: 'CreateList'})">
           <span>Created</span>
           <img src="@/assets/images/bottom.png" alt="">
         </div>
      </div>
      <div class="create">
        <div class="top">
          On The Currency
        </div>
        <div class="content-box">
          <van-cell-group title="Original Shares contract address(BSC Chain)">
            <van-field v-model="lockedToken" @update:model-value="searchToken" placeholder="Please enter contract address" />
          </van-cell-group>
          <van-cell-group title="Name of Circulating Shares">
            <van-field v-model="circulationTokenName" placeholder="Please enter name" />
          </van-cell-group>
             <van-cell-group title="Symbol of Circulating Shares">
            <van-field v-model="circulationTokenSympol" placeholder="Please enter symbol" />
          </van-cell-group>
          <van-cell-group title="Initial Circulation">
            <van-field v-model="preAmount" type="number" placeholder="Please enter quantity" />
          </van-cell-group>
        </div>
      
      </div>
      <div class="create">
        <div class="top">
           <img src="@/assets/images/biaswap.png" alt="">
        </div>
        <div class="content-box">
          <div class="create-pool">
            <div class="title">New token Initial Pool Creation</div>
            <div class="info">
              <div class="info-title flex-middle">
                <img src="@/assets/images/alert.png" alt="">
                <span>You are the first liquidity provider</span>
              </div>
              <div class="info-desc">
               The proportion of tokens you add will determine the initial price of the liquidity pool.<br>
               If you are satisfied with the exchange rate, click "Supply" to check.
              </div>
            </div>
             <div class="pool-box">
              <div class="input-item">
                <div class="token-info flex-between">
                  <div class="sympol flex-middle">
                    <img src="@/assets/images/no_token.png" alt="">
                    <span>{{circulationTokenSympol}} {{ circulationTokenSympol ? '(circulating shares)' : '-----'}}</span>
                  </div>
                  <div class="balance">Balance: {{!preAmount ? '0.00' : preAmount}}</div>
                </div>
                 <div class="input-box">
                   <input type="number" class="input" placeholder="Please enter quantity" v-model="circulationTokenAmount">
                   <div class="max-box">
                     <span>Max</span>
                   </div>
                 </div>
              </div>
              <div class="add-icon">
                <img src="@/assets/images/and.png" alt="">
              </div>
              <div class="input-item">
                <div class="token-info flex-between">
                  <div class="sympol flex-middle" @click="show = true">
                    <img :src="require(`@/assets/images/${selectTokenSymbol.toLowerCase()}.png`)" alt="">
                    <span>{{selectTokenSymbol}}</span>
                     <img class="arrow-bottom" src="@/assets/images/xiangxia.png" alt="">
                  </div>
                  <div class="balance">Balance: {{Number(tokenBalance).toFixed(2)}}</div>
                </div>
                 <div class="input-box">
                   <input type="number" class="input" placeholder="Please enter quantity" v-model="tokenToLiquidityAmount">
                   <div class="max-box">
                     <span>Max</span>
                   </div>
                 </div>
              </div>
              <div class="pool-info">
                <div class="pool-info-title">Initial price and liquidity pool share</div>
                <div class="pool-info-desc flex-middle">
                  <div class="item flex-item">
                    <div class="num">{{tokenUsdt}}</div>
                    <div class="desc">{{selectTokenSymbol}}/{{circulationTokenSympol}}</div>
                  </div>
                   <div class="item flex-item">
                    <div class="num">{{usdtToken}}</div>
                    <div class="desc">{{circulationTokenSympol}}/{{selectTokenSymbol}}</div>
                  </div>
                    <div class="item flex-item">
                    <div class="num">{{poolShare}}</div>
                    <div class="desc">pool share</div>
                  </div>
                </div>
              </div>
             </div>
          </div>
          <div class="primary-btn-box">
            <van-button type="primary" :loading="btnLoading" size="large" loading-text="Confirm..." :disabled="btnLoading" v-if="Number(tokenAllowance) > 0" block @click="createProject">Confirm</van-button>
            <van-button type="primary" :loading="btnLoading" size="large" loading-text="Approve..." :disabled="btnLoading" v-else block @click="approveToken">Approve {{selectTokenSymbol}}</van-button>
          </div>
        </div> 
      </div>
         <SelectToken :show="show" :token="selectToken" @updateShow="show = false" @changeToken="changeToken" />
        <waitingConfirmationfrom @updateShow="updateShow" :show="showConfirmation" :isWaiting="isWaiting"></waitingConfirmationfrom>
  </div>

</template>
<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import BigNumber from 'bignumber.js';
import SelectToken from "@/components/SelectToken"
import SeroFactoryAbi from "@/utils/SeroFactory"
import TokenAbi from "@/utils/abi"
import { MaxUint256 } from "@ethersproject/constants";
import { Toast } from 'vant';
import waitingConfirmationfrom from '@/components/waitingConfirmation'
import _ from 'lodash'

export default {
  components: {
    SelectToken,
    waitingConfirmationfrom
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      show: false,
      showConfirmation: false,
      isWaiting: false,
      usdtToken: computed(() => {
        if(!data.circulationTokenAmount || !data.tokenToLiquidityAmount) {
          return '---'
        }
        return Number(data.circulationTokenAmount/data.tokenToLiquidityAmount).toFixed(4)
      }),
      poolShare: computed(() => {
        if(!data.circulationTokenAmount || !data.tokenToLiquidityAmount) {
          return '---'
        }
        return '100%'
      }),
       tokenUsdt: computed(() => {
        if(!data.circulationTokenAmount || !data.tokenToLiquidityAmount) {
          return '---'
        }
        return Number(data.tokenToLiquidityAmount/data.circulationTokenAmount).toFixed(4)
      }),
      UsdtContract: null,
      SeroFactoryContract: null,
      circulationTokenSympol: '',
      circulationTokenName: '',
      preAmount: '',
      lockedToken: '',
      tokenToLiquidity: '',
      circulationTokenAmount: '',
      tokenToLiquidityAmount: '',
      tokenBalance: '',
      tokenAllowance: '0',
      btnLoading: false,
      selectToken: config.UsdtAddress,
      selectTokenSymbol: 'USDT',
      initContract: () => {
        data.SeroFactoryContract = new data.web3.eth.Contract(SeroFactoryAbi, config.seroFactoryAddress)
        data.UsdtContract = new data.web3.eth.Contract(TokenAbi, config.UsdtAddress)
        data.initData()
      },
      searchToken: _.throttle(async function(){
        try {
         let tokenContract = new data.web3.eth.Contract(TokenAbi, data.lockedToken)
         let name = await tokenContract.methods.name().call()
         let symbol = await tokenContract.methods.symbol().call()
         data.circulationTokenSympol = symbol
         data.circulationTokenName = name
        } catch {
          Toast('Invalid token address')
        }    
       }, 1500),
      updateShow: (value) => {
        data.showConfirmation = value
      },
      initData: () => {
        data.getTokenBalance()
        data.getTokenAllowance()
      },
      changeToken: async (token) => {
        data.show = false
        data.UsdtContract = new data.web3.eth.Contract(TokenAbi, token)
        data.selectToken = token
        data.selectTokenSymbol = await data.UsdtContract.methods.symbol().call();
        data.getTokenBalance()
        data.getTokenAllowance()
      },
      getTokenBalance: async () => {
        let decimals = await data.UsdtContract.methods.decimals().call();
        let balance = await data.UsdtContract.methods.balanceOf(data.address).call()
        data.tokenBalance = new BigNumber(balance).dividedBy(10**decimals).toFixed(Number(decimals))
      },
      getTokenAllowance: async () => {
        let allowance = await data.UsdtContract.methods.allowance(data.address, config.seroFactoryAddress).call();
        data.tokenAllowance = allowance;
        console.log(allowance)
      },
      approveToken: async () => {
        data.btnLoading = true
        try {
          await data.UsdtContract.methods.approve(config.seroFactoryAddress, MaxUint256).send({ from: data.address });
          data.getTokenAllowance()
          data.btnLoading = false
        } catch (error) {
          data.btnLoading = false
        }
      },
      createProject: async () => {
 
        if(!data.circulationTokenName) {
          Toast('Please enter name of Circulating shares')
          return
        }
         if(!data.circulationTokenSympol) {
          Toast('Please enter symbol of Circulating shares')
          return
        }
         if(!data.preAmount) {
          Toast('Please enter initial issuance of Circulating shares')
          return
        }
         if(!data.lockedToken) {
          Toast('Please enter original Shares contract address')
          return
        }
        //  if(!data.tokenToLiquidity) {
        //   return
        // }
         if(!data.circulationTokenAmount) {
          Toast('Please enter the amount to add liquidity')
          return
        }
         if(Number(data.circulationTokenAmount) > Number(data.preAmount)) {
          Toast('Not enough tokens')
          return
        }
         if(!data.tokenToLiquidityAmount) {
          Toast('Please enter the amount to add liquidity')
          return
        }
        if(Number(data.tokenToLiquidityAmount) > Number(data.tokenBalance)) {
          Toast('Not enough tokens')
          return
        }
        let isLock = await data.SeroFactoryContract.methods.lockAddressIsUsed(data.lockedToken).call()
        console.log(isLock)
        if(isLock) {
           Toast('locked Token is used')
          return
        }
        data.btnLoading = true
         let decimals = await data.UsdtContract.methods.decimals().call();
        let preAmount = new BigNumber(data.preAmount).multipliedBy(10**18).toFixed()
        let circulationTokenAmount = new BigNumber(data.circulationTokenAmount).multipliedBy(10**18).toFixed()
        let tokenToLiquidityAmount = new BigNumber(data.tokenToLiquidityAmount).multipliedBy(10**decimals).toFixed()
        try {
          let gas = await data.SeroFactoryContract.methods.createProjectAddLiquidity(
           data.circulationTokenSympol,
           data.circulationTokenName,
           preAmount,
           data.lockedToken,
           data.selectToken,
           circulationTokenAmount,
           tokenToLiquidityAmount
          ).estimateGas({from: data.address})
          console.log(gas)
          data.SeroFactoryContract.methods.createProjectAddLiquidity(
           data.circulationTokenSympol,
           data.circulationTokenName,
           preAmount,
           data.lockedToken,
           data.selectToken,
           circulationTokenAmount,
           tokenToLiquidityAmount
          ).send({from: data.address}).on('transactionHash', (hash) => {
              data.showConfirmation = false
              data.isWaiting = false
              data.showConfirmation = true
           })
          .on('receipt', (receipt) => {
            data.btnLoading = false
             store.commit('updateResult', {
               hash: receipt.transactionHash,
               time: new Date().getTime(),
               desc: 'Create project',
               isShow: true
             })
          })
          .on('error', () => {
            data.btnLoading = false
          })
        } catch (error) {
           data.btnLoading = false
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    onMounted(() => {
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
.create-box{
  margin: 0 0;
  // padding-bottom: 20px;
  .create{
    background: #fff;
    margin-bottom: 24px;
    border-radius: 24px;
    border: 1px solid #E7E3EB;
    .top{
      padding:24px;
      border-radius: 24px 24px 0 0;
      background: linear-gradient(103deg, #F2ECF2, #E8F2F6);
      font-size: 20px;
      color: #280D5F;
      font-weight: 700;
      img{
        width: 116px;
      }
    }
    .content-box{
       padding: 4px 24px 24px;
    }
  }
  .create-pool{
    padding: 20px 0 0;
    .title{
      color: var(--primary-color);
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 10px;
    }
    .info{
      background: var(--alert-color);
      border-radius: 12px;
      border: 1px solid var(--alert-border-color);
      padding: 15px;
      .info-title{
        margin-bottom: 10px;
        img{
          width: 12px;
        }
        span{
          font-size: 14px;
          color: var(--primary-color);
          font-weight: 600;
          margin-left: 5px;
        }
      }
      .info-desc{
         color: var(--primary-color);
         font-size: 13px;
         line-height: 20px;
      }
    }
  }
  .primary-btn-box{
    margin-top: 24px;
  }
}
.pool-box{
  padding-top: 20px;
  .input-item{
    .token-info{
      margin-bottom: 12px;
    }
    .sympol{
      img{
        width: 25px;
        height: 25px;
        margin-right: 6px;
      }
      span{
        color: var(--primary-color);
        font-size: 16px;
        font-weight: 600;
      }
      .arrow-bottom{
        width: 10px;
        height:  auto;
        margin-left: 5px;
      }
    }
    .balance{
       color: var(--primary-color);
       font-size: 13px;
    }
    .input-box{
      background: var(--input-background-color);
      border-radius: 12px;
      padding: 12px 15px;
      box-shadow: 0px 2px 5px 0px rgba(74, 74, 104, 0.1) inset;
      input{
        background: none;
        border: none;
        display: block;
        width: 100%;
        text-align: right;
         color: var(--primary-color);
         font-weight: bold;
      }
      .max-box{
        display: flex;
        flex-direction: row-reverse;
        margin-top: 8px;
        span{
          padding: 0px 6px;
          border-radius: 10px;
          line-height: 16px;
          border: 1px solid #1EC7D3;
          color: #1EC7D3;
          font-size: 10px;
        }
      }
    }
  }
  .add-icon{
    img{
     width: 32px;
     display: block;
     margin: 16px auto; 
    }
  }
  .pool-info{
    padding-top: 20px;
    .pool-info-title{
      font-size: 13px;
      font-weight: 600;
       color: var(--primary-color);
       margin-bottom: 15px;
    }
    .pool-info-desc{
      .item{
        text-align: center;
        .num{
          color: var(--primary-color);
          font-size: 14px;
          font-weight: bold;
        }
        .desc{
          color: var(--desc-color);
          font-size: 14px;
        }
      }
    }
  }
}
.create-btn{
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 15px;
  .btn{
    border: 1px solid #1FC7D4;
    font-size: 16px;
    font-weight: 600;
    color: #1FC7D4;
     padding: 4px 10px;
      border-radius: 16px;
    img{
      margin-left: 5px;
      width: 8px;
     
    }
  }
}
</style>
